import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function Signup(props) {
  const navigate = useNavigate();
  const [validated, setValidated] = useState(false);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
      setValidated(true);
      return;
    }
    axios
      .post("/api/users/", {
        user_name: name,
        email: email,
        user_password: password,
      })
      .then(function(response) {
        props.onAuth(response.data.token);
        navigate("/Users");
      })
      .catch(function(error) {
        console.log(error);
        alert("Error occurred");
      });
  };

  return (
    <div className="Auth-form-container">
      <Form
        noValidate
        validated={validated}
        onSubmit={handleSubmit}
        className="Auth-form"
      >
        <div className="Auth-form-content">
          <h3 className="Auth-form-title">–ó–∞—Ä—ç–≥—ñ—Å—Ç—Ä–∞–≤–∞—Ü—Ü–∞</h3>
          <div className="text-center">
            –£–∂–æ –∑–∞—Ä—ç–≥—ñ—Å—Ç—Ä–∞–≤–∞–ª—ñ—Å—è? {""}
            <span className="link-primary" onClick={props.onChangeAuthMode}>
              –£–≤–∞–π—Å—å—Ü—ñ
            </span>
          </div>
          <Form.Group className="mt-3">
            <Form.Label>–Ü–º—è</Form.Label>
            <Form.Control
              required
              onChange={(e) => setName(e.target.value)}
              value={name}
              type="text"
              className="mt-1"
              placeholder="–Ω–∞–ø—Ä—ã–∫–ª–∞–¥ –õ—è–≤–æ–Ω –ë–∞—Ä—à—á—ç—û—Å–∫—ñ"
            />
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label>Email –∞–¥—Ä–∞—Å</Form.Label>
            <Form.Control
              value={email}
              required
              onChange={(e) => setEmail(e.target.value)}
              type="email"
              className="mt-1"
              placeholder="Email –∞–¥—Ä–∞—Å"
            />
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label>–ü–∞—Ä–æ–ª—å</Form.Label>
            <Form.Control
              value={password}
              required
              onChange={(e) => setPassword(e.target.value)}
              type="password"
              className="mt-1"
              placeholder="–ü–∞—Ä–æ–ª—å"
            />
          </Form.Group>
          <div className="d-grid gap-2 mt-3">
            <button type="submit" className="btn btn-primary">
              –ü–∞–¥—Ü—å–≤–µ—Ä–¥–∑—ñ—Ü—å
            </button>
          </div>
        </div>
      </Form>
    </div>
  );
}
